// ==========================================
// RISIVO CRM - Power Dialer Stats/Analytics API
// ==========================================

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { DialerDashboardStats, CampaignStats, AgentStats } from '@/lib/power-dialer/types'

// Initialize Supabase client
const supabase = supabaseAdmin

// GET - Get dialer statistics
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const type = searchParams.get('type') || 'dashboard' // dashboard, campaign, agent, calls
    const campaignId = searchParams.get('campaignId')
    const agentId = searchParams.get('agentId')
    const dateFrom = searchParams.get('dateFrom')
    const dateTo = searchParams.get('dateTo')
    
    if (!subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'subAccountId is required'
      }, { status: 400 })
    }
    
    // Dashboard stats
    if (type === 'dashboard') {
      const stats = await getDashboardStats(subAccountId)
      return NextResponse.json({
        success: true,
        data: stats
      })
    }
    
    // Campaign stats
    if (type === 'campaign' && campaignId) {
      const stats = await getCampaignStats(campaignId)
      return NextResponse.json({
        success: true,
        data: stats
      })
    }
    
    // Agent stats
    if (type === 'agent') {
      const stats = await getAgentStats(subAccountId, agentId, dateFrom, dateTo)
      return NextResponse.json({
        success: true,
        data: stats
      })
    }
    
    // Call breakdown stats
    if (type === 'calls') {
      const stats = await getCallBreakdownStats(subAccountId, campaignId, dateFrom, dateTo)
      return NextResponse.json({
        success: true,
        data: stats
      })
    }
    
    return NextResponse.json({
      success: false,
      error: 'Invalid stats type'
    }, { status: 400 })
    
  } catch (error) {
    console.error('GET /api/v1/dialer/stats error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// Get dashboard overview stats
async function getDashboardStats(subAccountId: string): Promise<DialerDashboardStats> {
  const today = new Date()
  today.setHours(0, 0, 0, 0)
  const todayStr = today.toISOString()
  
  // Today's calls
  const { data: todayCalls } = await supabase
    .from('DialerCall')
    .select('status, outcome, talkDuration, isConnected')
    .eq('subAccountId', subAccountId)
    .gte('initiatedAt', todayStr)
  
  const todayStats = {
    totalCalls: todayCalls?.length || 0,
    answeredCalls: todayCalls?.filter(c => c.isConnected).length || 0,
    voicemails: todayCalls?.filter(c => c.outcome === 'voicemail').length || 0,
    avgCallDuration: 0,
    totalTalkTime: todayCalls?.reduce((sum, c) => sum + (c.talkDuration || 0), 0) || 0,
    connectRate: 0
  }
  
  if (todayStats.totalCalls > 0) {
    todayStats.connectRate = Math.round((todayStats.answeredCalls / todayStats.totalCalls) * 100)
  }
  if (todayStats.answeredCalls > 0) {
    todayStats.avgCallDuration = Math.round(todayStats.totalTalkTime / todayStats.answeredCalls)
  }
  
  // Active campaigns
  const { count: activeCampaigns } = await supabase
    .from('DialerCampaign')
    .select('*', { count: 'exact', head: true })
    .eq('subAccountId', subAccountId)
    .eq('status', 'active')
  
  // Active agents
  const { count: activeAgents } = await supabase
    .from('DialerAgentSession')
    .select('*', { count: 'exact', head: true })
    .eq('subAccountId', subAccountId)
    .in('status', ['available', 'on_call', 'wrap_up'])
  
  // Queued contacts
  const { count: queuedContacts } = await supabase
    .from('DialerQueue')
    .select('*', { count: 'exact', head: true })
    .eq('subAccountId', subAccountId)
    .eq('status', 'pending')
  
  // Calls in progress
  const { count: callsInProgress } = await supabase
    .from('DialerCall')
    .select('*', { count: 'exact', head: true })
    .eq('subAccountId', subAccountId)
    .in('status', ['initiated', 'ringing', 'in_progress'])
  
  return {
    today: todayStats,
    activeCampaigns: activeCampaigns || 0,
    activeAgents: activeAgents || 0,
    queuedContacts: queuedContacts || 0,
    callsInProgress: callsInProgress || 0
  }
}

// Get campaign-specific stats
async function getCampaignStats(campaignId: string): Promise<CampaignStats> {
  const { data: calls } = await supabase
    .from('DialerCall')
    .select('status, outcome, talkDuration, isConnected, dispositionCode')
    .eq('campaignId', campaignId)
  
  if (!calls || calls.length === 0) {
    return {
      totalCalls: 0,
      answeredCalls: 0,
      voicemails: 0,
      noAnswers: 0,
      busySignals: 0,
      failedCalls: 0,
      avgCallDuration: 0,
      totalTalkTime: 0,
      connectRate: 0,
      conversionRate: 0
    }
  }
  
  const stats: CampaignStats = {
    totalCalls: calls.length,
    answeredCalls: calls.filter(c => c.isConnected).length,
    voicemails: calls.filter(c => c.outcome === 'voicemail').length,
    noAnswers: calls.filter(c => c.outcome === 'no_answer').length,
    busySignals: calls.filter(c => c.outcome === 'busy').length,
    failedCalls: calls.filter(c => c.outcome === 'failed').length,
    totalTalkTime: calls.reduce((sum, c) => sum + (c.talkDuration || 0), 0),
    avgCallDuration: 0,
    connectRate: 0,
    conversionRate: 0
  }
  
  if (stats.totalCalls > 0) {
    stats.connectRate = Math.round((stats.answeredCalls / stats.totalCalls) * 100)
  }
  
  if (stats.answeredCalls > 0) {
    stats.avgCallDuration = Math.round(stats.totalTalkTime / stats.answeredCalls)
    
    // Conversion = positive dispositions / answered calls
    const positiveDispositions = calls.filter(c => 
      ['APPT', 'SALE', 'INT', 'INFO'].includes(c.dispositionCode || '')
    ).length
    stats.conversionRate = Math.round((positiveDispositions / stats.answeredCalls) * 100)
  }
  
  return stats
}

// Get agent performance stats
async function getAgentStats(
  subAccountId: string, 
  agentId?: string | null,
  dateFrom?: string | null,
  dateTo?: string | null
): Promise<AgentStats[]> {
  let query = supabase
    .from('DialerCall')
    .select('agentId, agentName, status, outcome, talkDuration, isConnected, dispositionCode')
    .eq('subAccountId', subAccountId)
    .not('agentId', 'is', null)
  
  if (agentId) {
    query = query.eq('agentId', agentId)
  }
  
  if (dateFrom) {
    query = query.gte('initiatedAt', dateFrom)
  }
  
  if (dateTo) {
    query = query.lte('initiatedAt', dateTo)
  }
  
  const { data: calls } = await query
  
  if (!calls || calls.length === 0) {
    return []
  }
  
  // Group by agent
  const agentMap = new Map<string, {
    agentName: string
    calls: typeof calls
  }>()
  
  calls.forEach(call => {
    if (!call.agentId) return
    
    if (!agentMap.has(call.agentId)) {
      agentMap.set(call.agentId, {
        agentName: call.agentName || 'Unknown',
        calls: []
      })
    }
    agentMap.get(call.agentId)!.calls.push(call)
  })
  
  // Calculate stats per agent
  const agentStats: AgentStats[] = []
  
  agentMap.forEach((data, agentId) => {
    const agentCalls = data.calls
    const answeredCalls = agentCalls.filter(c => c.isConnected).length
    const totalTalkTime = agentCalls.reduce((sum, c) => sum + (c.talkDuration || 0), 0)
    
    // Count dispositions
    const dispositions: Record<string, number> = {}
    agentCalls.forEach(c => {
      if (c.dispositionCode) {
        dispositions[c.dispositionCode] = (dispositions[c.dispositionCode] || 0) + 1
      }
    })
    
    agentStats.push({
      agentId,
      agentName: data.agentName,
      totalCalls: agentCalls.length,
      answeredCalls,
      avgCallDuration: answeredCalls > 0 ? Math.round(totalTalkTime / answeredCalls) : 0,
      totalTalkTime,
      dispositions
    })
  })
  
  // Sort by total calls descending
  agentStats.sort((a, b) => b.totalCalls - a.totalCalls)
  
  return agentStats
}

// Get call breakdown by outcome/disposition
async function getCallBreakdownStats(
  subAccountId: string,
  campaignId?: string | null,
  dateFrom?: string | null,
  dateTo?: string | null
) {
  let query = supabase
    .from('DialerCall')
    .select('outcome, dispositionCode, dispositionName')
    .eq('subAccountId', subAccountId)
  
  if (campaignId) {
    query = query.eq('campaignId', campaignId)
  }
  
  if (dateFrom) {
    query = query.gte('initiatedAt', dateFrom)
  }
  
  if (dateTo) {
    query = query.lte('initiatedAt', dateTo)
  }
  
  const { data: calls } = await query
  
  if (!calls || calls.length === 0) {
    return {
      byOutcome: {},
      byDisposition: {},
      total: 0
    }
  }
  
  // Count by outcome
  const byOutcome: Record<string, number> = {}
  calls.forEach(c => {
    const outcome = c.outcome || 'unknown'
    byOutcome[outcome] = (byOutcome[outcome] || 0) + 1
  })
  
  // Count by disposition
  const byDisposition: Record<string, { count: number; name: string }> = {}
  calls.forEach(c => {
    if (c.dispositionCode) {
      if (!byDisposition[c.dispositionCode]) {
        byDisposition[c.dispositionCode] = {
          count: 0,
          name: c.dispositionName || c.dispositionCode
        }
      }
      byDisposition[c.dispositionCode].count++
    }
  })
  
  return {
    byOutcome,
    byDisposition,
    total: calls.length
  }
}
